/**
 * CSS
 ============================================================================ */
import "./Help.css";

/**
  * System imports
  ============================================================================ */
import React from "react";
import { useTranslation, Trans } from "react-i18next";
// import { Container, Row, Col } from "react-bootstrap";

/**
   * User imports
   ============================================================================ */
import Explorer from "../explorer/Explorer";

/**
   * Default export
   ============================================================================ */
export default (args) => {
  /**
     * Default Properties
     ============================================================================ */
  let defaults = { namespace: "Help", searchable: true };

  /**
     * Properties
     ============================================================================ */
  const props = Object.assign(defaults, args);

  /**
     * Barrels
     ============================================================================ */
  const {
    helpPath1,
    helpPath2,
    countSortableListItems,
    topicsPerPage,
    activePageTopics,
    countTexts,
    textItemsPerPage,
    activePageTexts,
    textTitlesList,
    topicsList,
  } = props.barrelGetters;
  const {
    setTextTitle,
    setTextAppetizer,
    setTextDescription,
    setTextTitlesList,
    setTopicsList,
  } = props.barrelSetters;
  const {
    setHelpPath,
    getUserPermissionStatus,
    getTopics,
    getTextsList,
    getFileLinkFromURI,
  } = props.barrelFunctions;
  // const {} = props.barrelEventHandlers;
  const { pathJobCategory, pathHelpCategory } = props.barrelProps;

  /**
     * Module vars
     ============================================================================ */
  const { t } = useTranslation([props.namespace]);

  /**
     * Functions
     ============================================================================ */

  /**
   * On view all jobs click
   */
  const onViewAllJobsClick = () => {
    setHelpPath(2, "");
  };

  /**
   * On job save
   * @param {*} param0
   */
  const onJobSave = ({ title, appetizer, text }) => {
    setTextTitle(title);
    setTextAppetizer(appetizer);
    setTextDescription(text);
  };

  /**
   * URL encode string
   * @param {*} rawStr
   */
  const urlEncode = (rawStr) => {
    return rawStr.replace(/\+/g, "%2B");
  };

  /**
   * On Search submit
   * @param {*} search
   */
  const onSearchSubmit = (search) => {
    const searchUrlEncoded = urlEncode(search);
    window.location = `/${pathHelpCategory}?search=${searchUrlEncoded}`;
  };

  const noFilesFoundMessage = (
    <Trans i18nKey="Help:Not found">
      No articles found using your search criteria. Please try different
      criteria or click{" "}
      <a href={`/${pathHelpCategory}?search=`} onClick={onViewAllJobsClick}>
        here
      </a>{" "}
      to view all articles.
    </Trans>
  );

  const onHelpPath1Click = (link) => {
    setHelpPath(1, link);
  };

  const navigation = false
    ? [
        {
          name: t("Personal Help"),
          link: "#personal",
        },
        {
          name: t("Business Help"),
          link: "#business",
        },
        {
          name: t("Technical Help"),
          link: "#technical",
        },
      ]
    : false;

  /**
     * Return
     ============================================================================ */
  return (
    <>
      <Explorer
        t={t}
        searchable={props.searchable}
        searchHeadline={t("How can we help?")}
        title={t("Help Center")}
        navigation={navigation}
        navigationdefaultActiveKey={helpPath1}
        onNavigationClick={onHelpPath1Click}
        titleFolders={false}
        titleNewFile={t("New Article")}
        folderList={topicsList}
        fileList={textTitlesList}
        fileTitle={t(helpPath2)}
        fileDate={t("Date Posted")}
        setFolderList={setTopicsList}
        setFileList={setTextTitlesList}
        showFiles={textTitlesList !== null}
        filesTableStriped={false}
        filesTableHover={false}
        filesTableShowDate={false}
        fileSaveRequest="save text"
        fileUpdateRequest="update texts"
        folderSaveRequest="save topics"
        fileSaveRequestData={{
          id: null,
          type: helpPath2,
          status: 0,
        }}
        folderSaveRequestData={{
          cat1: "help",
          cat2: pathHelpCategory,
        }}
        foldersPath={`/${pathHelpCategory}`}
        filesPath="/help"
        foldersCountFilesField="count_texts"
        fileIdField="textid"
        foldersEditAllowed={getUserPermissionStatus(11)}
        folderName={t("Topic")}
        folderLink={t("Link")}
        foldersShowFilesCount={false}
        noFilesFoundMessage={noFilesFoundMessage}
        placeholderSearch={t("Job Title or Category")}
        countFolders={countSortableListItems}
        foldersPerPage={topicsPerPage}
        activeFoldersPage={activePageTopics}
        onFoldersPageClick={(number) => getTopics({ newPage: number })}
        onFolderClick={(link) => setHelpPath(2, link)}
        countFiles={countTexts}
        filesPerPage={textItemsPerPage}
        activeFilesPage={activePageTexts}
        onFilesPageClick={(number) => getTextsList({ newPage: number })}
        onFileSave={onJobSave}
        onResetFilters={onViewAllJobsClick}
        onSearchSubmit={onSearchSubmit}
        {...args}
      />
    </>
  );
};
